button{
    font-size: medium;
    font-weight: bold;
    border:none;
    width:150px;
    height:55px;
    margin:2%;
    background-color: rgba(236, 230, 230, 0.89);
    box-shadow:0 1px 5px rgba(0,0,0,.5);
    border-radius: 3px;
    outline:none;
}
.selected{
    font-size: medium;
    font-weight: bold;
    border:none;
    width:150px;
    height:55px;
    margin:2%;
    background-color: rgba(2, 204, 46, 0.829);
    box-shadow:0 1px 5px rgba(0,0,0,.5);
    border-radius: 3px;
    outline:none;
    color:whitesmoke
}
.submit{
    font-size: medium;
    font-weight: bold;
    border:none;
    width:200px;
    height:50px;
    margin:10px;
    background-color: rgba(28, 29, 28, 0.829);
    color:white;
    box-shadow:0 5px 5px rgba(0,0,0,.5);
    border-radius: 5px;
    outline:none;
}