.Container{
   
   
    background-color: rgb(255, 255, 255);
    margin-left: 15%;
    margin-right:15%;
    padding:2%;
    display: grid;
    grid-template-columns: 50% 50%;
    margin-bottom: 25px;
    box-shadow:0 6px 15px rgba(26, 25, 25, 0.452);
    border-radius: 2px 2px 15px 15px;
    border:0.5px solid rgba(211, 209, 209, 0.918);
}
.imgContainer{
    display: grid;
    grid-template-columns: 50% 50%;
    margin-left: 0px;
    border-bottom:1px solid black;
    text-align: initial;
    padding-left: 5%;

}
.Label{
    margin-top:15%;
    color:rgba(236, 16, 16, 0.932);
    font-weight: 600;
    font-size: xx-large;
    text-align: center;
    
    
}
.IMG{
margin-left:0;
}
.select{
    margin-top:50px;
    margin-left:40%;
    font-size: x-large;
    width:50%;
    background-color: rgba(28, 29, 28, 0.829);
    color:white;
    font-weight: 500;
    box-shadow:0 5px 10px rgba(0,0,0,.5);

}
.nameContainer{
    background-color:rgb(255, 255, 255);
    display: grid;
    grid-template-columns: 70% 30%;
}
.summary{
    text-align: initial;
    margin-left:5%;
    
}
.price{
   font-size: 400;
   font-family: sans-serif;
    padding-top:10px;
    font-weight: 500;
}
@media  screen and (max-width:700px){
    .Container{
        background-color: rgb(255, 255, 255);
        margin-left: 10%;
        margin-right:10%;
        padding:2%;
        display: grid;
        grid-template-columns: 100%;
        margin-bottom: 35px;
    }
    .select{
        margin-left:17%;
        width:80%;
        font-size: x-large;
        width:65%;
        box-shadow:0 5px 10px rgba(0,0,0,.5);
    }
}
