.FoodItemsContainer{
    display:grid;
    grid-template-columns: 30% 30% 30%;
    padding-top:50px;
    padding-left:10%;
    background-color: rgba(255, 255, 255, 0.986);
}
.Btn{
    width:30px;
    margin:10px;
    height:30px;
    background-color: rgba(224, 214, 218, 0.171);
}
.buttonContainer{
    margin-bottom:5%;
}
.FoodItem{
    border-radius: 0px 0px 15px 15px;
    border-bottom:35px solid rgba(28, 29, 28, 0.541);
    margin:5%;
    margin-top:2%;
    box-shadow:0 5px 10px rgba(0,0,0,.5);
}
.Image{
    width:100%;
    height:auto;
}
@media screen and (max-width:1024px){
    .FoodItemsContainer{
        display:grid;
        grid-template-columns: 50% 50%;
      
        padding:15%;
        padding-left:18%;
        padding-top:5%;
        
    }
    .Image{
        width:100%;
        height:auto;
    }
    
}

@media screen and (max-width:700px){
    .FoodItemsContainer{
        display:grid;
        grid-template-columns: 100%;
      
        padding:15%;
        padding-left:18%;
        padding-top:5%;
        
    }
    .Image{
        width:100%;
        height:auto;
    }
    
}

